export default {
	name:"Index",
	data() {
		return {
			
		};
	},
	onLoad(options){
		uni.setNavigationBarColor({
			backgroundColor: "#ffffff",
			frontColor: '#000000',
			animation: {
				duration: 100,
				timingFunc: 'easeIn'
			}
		})
		var access_token = uni.getStorageSync('access_token');
		if(!access_token){
			this.$acFrame.getToken().then(res => {
				if(res.success){
					uni.switchTab({
						url:'../home/index'
					})
				}
			})
		}else{
			uni.switchTab({
				url:'../home/index'
			})
		}
	}
}
